import React, { useEffect, useState } from 'react'
import {
    CRow,
    CCol,
    CCard,
    CCardHeader,
    CCardBody,
    CLabel,
    CInput,
    CInputGroup,
    CButton,
    CSelect,
} from '@coreui/react'

import { ColorPicker, useColor, toColor } from 'react-color-palette'
import "react-color-palette/lib/css/styles.css"

var Color = require('color');

const ColorConverter = () => {
    const [colorPalette, setColorPalette] = useColor("hex", "#121212");
    const [color, setColor] = useState(Color("#121212"))
    const [hex, setHEX] = useState("")

    const setColorHEX = (e) => {
        var data = e.target && e.target.value;
        setHEX(data);

        if (data && data.length === 7) {
            setColorPalette(toColor("hex", data));
        }
    }

    useEffect(() => {
        if (!color) return;
        console.log(color.toJSON());
    }, [color])

    useEffect(() => {
        setColor(Color(colorPalette.hex))
    }, [colorPalette])

    return (
        <CRow>
            <CCol lg={12}>
                <CCard>
                    <CCardBody>
                        <CRow>
                            <CCol lg={8}>
                                <CCard key='color_converter' style={{ marginBottom: 0 }} >
                                    <CCardHeader>
                                        Color Converter
                                    </CCardHeader>
                                    <CCardBody>
                                        <CRow style={{ padding: '1rem 0 1rem 0', lineHeight: '35px' }}>
                                            <CCol md="2">
                                                <CSelect custom name="src_color" id="src_color" onChange={(e) => { alert(e) }}>
                                                    <option value="HEX"> HEX </option>
                                                    <option value="RGB"> RGB </option>
                                                    <option value="HSV"> HSV </option>
                                                    <option value="CMYK"> CMYK </option>
                                                </CSelect>
                                            </CCol>
                                            <CCol xs="12" md="8">
                                                <CInputGroup>
                                                    <CInput id="hex" value={hex} onChange={setColorHEX}></CInput>
                                                </CInputGroup>
                                                <span className="input_hex">Input Hex string '#xxxxxx' or 'xxxxxx' </span>
                                            </CCol>
                                            <CCol xs="12" md="2">
                                                <CButton color="primary"> Convert </CButton>
                                            </CCol>
                                        </CRow>
                                        {color && <>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HEX </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <span>{`${color.hex()}`}</span>
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> RGB </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <span>{`${color.rgb().model} [${color.rgb().color}]`}</span>
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HSV </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <span>{`${color.hsv().model} [${color.hsv().color}]`}</span>
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HSl </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <span>{`${color.hsl().model} [${color.hsl().color}]`}</span>
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> CMYK </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <span>{`${color.cmyk().model} [${color.cmyk().color}]`}</span>
                                                </CCol>
                                            </CRow>
                                        </>
                                        }
                                    </CCardBody>
                                </CCard>
                            </CCol>
                            <CCol lg={4} style={{ display: 'flex', justifyContent: 'space-around' }}>
                                <ColorPicker width={400} height={256} color={colorPalette} onChange={setColorPalette} hideHSV hideRGB hideHEX dark />
                            </CCol>
                        </CRow>
                    </CCardBody>
                </CCard>
            </CCol>
        </CRow>
    )
}

export default ColorConverter
